<template>
  <div class="filters">
    <input
      type="text"
      name="search"
      placeholder="Поиск по коду сертификата, коду бизнес зала, названию бизнес зала"
      class="filters__search"
      v-model="search"
    />
    <div class="filters__controls">
      <h3 class="filters__subtitle">Период</h3>
      <ul class="filters__list">
        <li class="filters__item">
          <input
            type="radio"
            name="period"
            id="yesterday-filter"
            value="yesterday-filter"
            v-model="periods"
            checked
          />
          <label class="filters__label" for="yesterday-filter">Вчера</label>
        </li>
        <li class="filters__item">
          <input
            type="radio"
            name="period"
            id="today-filter"
            value="today-filter"
            v-model="periods"
          />
          <label class="filters__label" for="today-filter">Сегодня</label>
        </li>
        <li class="filters__item">
          <input
            type="radio"
            name="period"
            id="month-filter"
            value="month-filter"
            v-model="periods"
          />
          <label class="filters__label" for="month-filter">Месяц</label>
        </li>
        <li class="filters__item">
          <input
            type="radio"
            name="period"
            id="year-filter"
            value="year-filter"
            v-model="periods"
          />
          <label class="filters__label" for="year-filter">Год</label>
        </li>
        <li class="filters__item">
          от
          <DatePicker
            class="filters__date-picker"
            v-model="timeFrom"
            mode="date"
          >
            <template v-slot="{ inputValue, inputEvents }">
              <input
                class="filters__date-input"
                :value="inputValue"
                v-on="inputEvents"
                placeholder="дд.мм.гггг"
              />
            </template>
          </DatePicker>
        </li>
        <li class="filters__item">
          до
          <DatePicker class="filters__date-picker" v-model="timeTo" mode="date">
            <template v-slot="{ inputValue, inputEvents }">
              <input
                class="filters__date-input"
                :value="inputValue"
                v-on="inputEvents"
                placeholder="дд.мм.гггг"
              />
            </template>
          </DatePicker>
        </li>
      </ul>
    </div>
    <div class="filters__controls">
      <h3 class="filters__subtitle">Статус</h3>
      <ul class="filters__list">
        <li class="filters__item">
          <input type="radio" name="status" id="all-filter" checked />
          <label class="filters__label" for="all-filter">Все</label>
        </li>
        <li class="filters__item">
          <input type="radio" name="status" id="active-filter" />
          <label class="filters__label" for="active-filter"
            >Активированные</label
          >
        </li>
        <li class="filters__item">
          <input type="radio" name="status" id="decline-filter" />
          <label class="filters__label" for="decline-filter">Отказ</label>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      timeFrom: null,
      timeTo: null,
      period: null,
      status: null,
      periods: 'yesterday-filter',
      search: '',
    }
  },
  watch: {
    timeFrom(newV) {
      if (newV || this.timeFrom || this.timeTo) {
        this.periods = ''
      }
    },
    timeTo(newV) {
      if (newV || this.timeFrom || this.timeTo) {
        this.periods = ''
      }
    },
    periods(newV) {
      if (newV) {
        this.timeFrom = null
        this.timeTo = null
      }
    },
  },
  methods: {
    setPeriod(e) {
      console.log(e)
    },
  },
}
</script>
