// import axios from 'axios'
import Vue from 'vue'

const initialState = {
  data: null,
  hall: null,
  totalCertificates: 0,
  totalVisits: 0,
}

export default {
  state: () => initialState,

  mutations: {
    setRegistryData(state, payload) {
      state.data = payload.data
      state.hall = payload.hall
      state.totalCertificates = payload.data.hall.certificates_count
      state.totalVisits = payload.data.hall.certificates_sum_pass_limit
    },
  },

  actions: {
    async fetchRegistryData({ commit }) {
      let resp
      try {
        resp = await Vue.prototype.$axios.get('/hall/certificates')

        const { data } = resp.data

        commit('setRegistryData', { data })
      } catch (e) {
        if (e.response.status === 401) {
          commit('logout')
        }
      }
    },
  },

  getters: {
    getRegistryData: (s) => s.data,
    getRegistryName: (s) => (s.data && s.data.name ? s.data.name : ''),
    getRegistryCertificates: (s) =>
      s.data && s.data.certificates ? s.data.certificates : [],
    getTotalCertificates: (s) => s.totalCertificates,
    getTotalVisits: (s) => s.totalVisits,
  },
}
